.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--whiteish_color);
  border-radius: 15px;
  height: 290px; }

.food {
  position: relative;
  height: 170px;
  width: 100%;
  object-fit: cover;
  object-position: center center;
  z-index: 1; }

.text-overflow-center {
  background: var(--grayish_color);
  color: white;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  padding: 0px 3px 0px 12px;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  text-align: center;
  font-size: x-large; }

.diet {
  margin-left: 2px;
  margin-right: 2px;
  font-size: 17px;
  color: white;
  white-space: nowrap;
  background: #d3b468;
  padding: 2px 4px 2px 4px;
  margin: 3px 3px 3px 3px;
  border-radius: 8px; }

.nDiet {
  margin-left: 2px;
  margin-right: 2px;
  font-size: 17px;
  color: white;
  white-space: nowrap;
  background: #b57171;
  padding: 2px 4px 2px 4px;
  margin: 3px 3px 3px 3px;
  border-radius: 8px; }

// ------- Heart types ---------
#heart-red {
  filter: hue-rotate(10deg) saturate(6.5);
  margin-top: 225px;
  margin-left: 140px;
  position: absolute;
  width: 50px;
  height: 50px; }

#heart-yellow {
  filter: hue-rotate(60deg) saturate(4) brightness(1);
  margin-top: 225px;
  margin-left: 140px;
  position: absolute;
  width: 50px;
  height: 50px; }

#heart-orange {
  filter: hue-rotate(35deg) saturate(3);
  margin-top: 225px;
  margin-left: 140px;
  position: absolute;
  width: 50px;
  height: 50px; }

#heart-green {
  filter: hue-rotate(100deg) saturate(2.5);
  margin-top: 225px;
  margin-left: 140px;
  position: absolute;
  width: 50px;
  height: 50px; }

